.cart-dropdown {
  position: absolute;
  width: 450px;
  height: max-content;
  display: flex;
  flex-direction: column;
  padding: 20px;
  border: 1px solid black;
  background-color: white;
  top: 90px;
  right: 40px;
  z-index: 5;
  text-align: center;

  .empty-message {
    font-size: 18px;
    margin: 15px auto;
    font-family: f1_2;
  }

  .cart-items {
    height: 400px;
    display: flex;
    flex-direction: column;
    overflow: scroll;
  }

